import React, { useState } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import Constants from '../../constants'
import Definitions from '../../constants/definitions'
import Loading from '../widgets/Loading'

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function InfoDialog({open, setOpen, dialogInfo}) {

  let [note, setNote] = useState(true)
  
  let [loading, setLoading] = useState(false)
    
  const handleClose = () => {
    if(dialogInfo.type === Constants.RESULT && note){
      setLoading(true)
      setTimeout(()=>{
          setLoading(false)
          setNote(false)
      }, 1000) 
    }else{
        setOpen(false);
        setTimeout(()=>{
            setNote(true)
        }, 500)
    }
  };

  return (
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title">{dialogInfo.title}</DialogTitle>
        <DialogContent>
          {dialogInfo.type !== Constants.RESULT ? <DialogContentText id="alert-dialog-slide-description">
            {dialogInfo.info}
          </DialogContentText> 
          : !note ? <DialogContentText id="alert-dialog-slide-description" variant="h4" align="center" >  
            {dialogInfo.info}
          </DialogContentText> 
          : <DialogContentText id="alert-dialog-slide-description">
                {!loading ? Definitions[Constants.NOTE] : <Loading />}
            </DialogContentText>}
        </DialogContent>
        <DialogActions>
          {!loading ? <Button onClick={handleClose} color="primary">
            OK
          </Button> : null }
        </DialogActions>
      </Dialog>
  );
}